.section-1 {
    text-align: center;
    margin-left: 5rem;
    padding-bottom: 80px;
    padding-top: 80px;
}

.section-2 {
    background-color: lightgray;
    text-align: justify;
    margin-left: 5rem;
    margin-top: 150px;
    padding-bottom: 80px;
    padding-top: 80px;
}

.section-3 {
    margin-left: 5rem;
    padding-bottom: 80px;
    padding-top: 80px;
}

.section-4 {
    background-color: rgb(11, 19, 19);
    margin-left: 5rem;
    padding-top: 80px;
    padding-bottom: 80px;
    text-align: center;
    color: white;
}

.navbar {
    width: 5rem;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background-color: #18181d;
    transition: width 0.3s ease;
    opacity: 0.9;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar:hover {
    transition: 0.5s;
    width: 12rem;
    opacity: 1;
}

.navbar:hover .link-text-first {
    display: block;
}

.navbar-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav-link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 5rem;
    width: 7.7rem;
    text-decoration: none;
    color: #b6b6b6;
    filter: grayscale(100%) opacity(0.9);
}

.nav-link:hover {
    filter: grayscale(0%) opacity(1);
    background-color: #1c1c24;
    color: cyan;
    align-items: center;
    justify-content: center;
    transition: width 0.8s;
    width: 12rem;
}

.link-text {
    display: none;
    margin-left: 1.5rem;
    color: cyan;
}

.navbar:hover .link-text {
    display: block;
}

.name {
    font-size: 0.875rem;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
}

.name-desc {
    font-family: 'Secular One', sans-serif;
    font-size: 35px;
}

.arrow-down {
    color: black;
}

.arrow-down:hover {
    color: cyan;
}

.about-me-title {
    font-family: 'Raleway', sans-serif;
    font-size: 35px;
    text-decoration: underline;
}

.about-me-desc {
    font-family: 'Secular One', sans-serif;
    font-size: 35px;
}

.my-work-title {
    font-family: 'Raleway', sans-serif;
    font-size: 30px;
    opacity: 0.6;
    text-decoration: underline;
}

.my-work-desc {
    font-family: 'Cabin', sans-serif;
    font-size: 40px;
    margin-bottom: 50px;
}

.contact-icon {
    color: white;
}

.contact-icon:hover {
    color: cyan;
}

.designed {
    padding-top: 80px;
}

.designed:hover {
    color: cyan;
}